// @flow

import { getHDSetting, getDecoder } from '../core/networkInfo.js'
import {
  type ExtendedPrivateKey,
  type ExtendedPublicKey
} from '../../types/hd.js'
import { getTweakPoint, dataFromHex, dataToHex, XKEY_DEFAULTS, HARDENED, deriveKey } from './common.js'
import { toPublic } from '../core/privateKey.js'
import { hash160 } from '../utils/hash.js'
import { privateKeyTweakAdd } from '../utils/secp256k1.js'
// publicKeyTweakAdd

export const SEED = '426974636f696e2073656564'

export const deriveIndex = async (parentKey: ExtendedPrivateKey, index: number): Promise<ExtendedPrivateKey> => {
  if (!parentKey.publicKey) {
    parentKey.publicKey = await toPublic(parentKey.privateKey, true)
  }

  const { privateKey, publicKey, chainCode, version, depth } = parentKey
  // Default value for the key we're using to get the tweak point
  let tweakKey = `00${privateKey}`
  // If the index is non-hardended, set 'key' to be the publicKey if exists
  // Or try to create it from the privateKey
  if (index < HARDENED) tweakKey = publicKey

  const child = await deriveKey(privateKey, tweakKey, chainCode, index, privateKeyTweakAdd)

  const parentFingerPrint = hash160(publicKey).slice(0, 8)
  return {
    privateKey: child.key,
    chainCode: child.chainCode,
    childNumber: child.childNumber,
    parentFingerPrint: parseInt(parentFingerPrint, 16),
    version,
    depth: depth + 1
  }
}

export const fromKey = (key: $Shape<ExtendedPrivateKey>, network?: string): ExtendedPrivateKey => {
  if (!key.chainCode) return fromSeed(key.privateKey)
  const xKey = { ...XKEY_DEFAULTS, ...key }
  if (!xKey.version) {
    const { xpriv: { prefix } } = getHDSetting('xprv', network)
    xKey.version = prefix
  }
  return xKey
}

export const fromSeed = (
  seed: string,
  network?: string,
  version: string = 'xprv'
): ExtendedPrivateKey => {
  const { point: privateKey, chainCode } = getTweakPoint(seed, SEED)
  const { xpriv: { prefix } } = getHDSetting(version, network)
  return {
    ...XKEY_DEFAULTS,
    privateKey,
    chainCode,
    version: prefix
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedPrivateKey => {
  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte !== 0) throw new Error('Wrong private key header')

  return {
    privateKey: keyHex.slice(92, 156),
    ...dataFromHex(keyHex, network)
  }
}

export const toHex = (key: ExtendedPrivateKey): string => {
  if (key.privateKey.length !== 64) throw new Error('Wrong private key length')
  return dataToHex(key) + `00${key.privateKey}`
}

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedPrivateKey => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}

export const toString = (
  hdKey: ExtendedPrivateKey,
  network: string = 'main'
): string => getDecoder(network, hdKey.version).encode(toHex(hdKey))

export const toXPub = async (hdKey: ExtendedPrivateKey, network?: string): Promise<ExtendedPublicKey> => {
  const { privateKey, ...rest } = hdKey
  const { xpub: { prefix } } = getHDSetting(hdKey.version, network)
  let { publicKey } = hdKey
  if (!publicKey) publicKey = await toPublic(privateKey, true)

  const result = {
    ...rest,
    version: prefix,
    publicKey
  }
  return result
}
