// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as Private from '../../src/hd/private.js'
// import { deriveKeyPair } from '../../src/hd/common.js'
import fixtures from './fixtures/private.json'

const { network } = fixtures

describe('Testing HD Private Key', function () {
  fixtures.string.forEach(test => {
    const base58Key = test[0]
    const privateKey = {
      privateKey: test[1],
      version: test[2],
      depth: test[3],
      parentFingerPrint: test[4],
      childNumber: test[5],
      chainCode: test[6]
    }

    it(`Extended key from string: ${test[0]}`, function () {
      const resultedXkey = Private.fromString(base58Key, network)
      assert.deepEqual(resultedXkey, privateKey)
    })

    it(`Extended key to string: ${test[1]}`, function () {
      const resultedBase58Key = Private.toString(privateKey, network)
      assert.equal(resultedBase58Key, base58Key)
    })
  })

  fixtures.derive.forEach(test => {
    it(`Deriving key ${test[0]} for index ${test[2]}`, async function () {
      const privateKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const publicKey = test[6]
      const expectedChildKey = {
        privateKey: test[3],
        chainCode: test[4],
        childNumber: test[5]
      }
      const xKey = Private.fromKey({ privateKey, chainCode, publicKey })
      const childKey = await Private.deriveIndex(xKey, index)
      assert.deepEqual(childKey, expectedChildKey)
    })
  })
})
